import { DEVICE_STATE } from '@/utils/constantsFile';
import { debounce } from '@/utils/debounce';
// 甘特图图例颜色
const color = ['', '#0871dc', '#f39800', '#666666', '#ff0000'];
// loading图标选项
const loadingOption = {
  text: '',
  spinnerRadius: 20,
  color: '#0871dc'
};
export default {
  name: 'echarts-gantt',
  props: ['chartData', 'loading'],
  data() {
    return {
      myChart: null,
      // 没有数据的标志，显示无数据
      noData: false
    };
  },
  watch: {
    chartData: function() {
      if (this.chartData) {
        this.draw();
        this.noData = false;
      } else {
        this.noData = true;
      }
    },

    loading: function(current) {
      if (!this.myChart) { this.myChart = this.$echarts.init(this.$refs.echartGantt); }
      if (current) {
        this.myChart.showLoading('default', loadingOption);
      } else {
        this.myChart.hideLoading();
      }
    }
  },
  mounted() {
    this.draw();
  },
  methods: {
    draw() {
      if (!this.chartData) return;
      this.myChart = this.$echarts.init(this.$refs.echartGantt);
      const legendData = [];
      const series = [];
      for (const [key, value] of Object.entries(DEVICE_STATE)) {
        legendData.push({
          name: value,
          icon: 'rect'
        });
        series.push({
          name: value,
          type: 'custom',
          renderItem: this.renderRect,
          color: color[key],
          encode: {
            x: [1, 2],
            y: 0
          },
          data: []
        });
      }
      this.formatMaintData(this.chartData).map(el => {
        series[el.name] && series[el.name].data.push(el);
      });

      console.log(series);

      this.myChart.setOption({
        tooltip: {
          formatter: function(params) {
            return `${params.seriesName}:${params.value[1]} - ${params.value[2]}`;
          }
        },
        legend: {
          show: true,
          top: '5%',
          left: 10,
          textStyle: {
            fontSize: 14
          },
          data: legendData
        },
        toolbox: {
          show: true,
          right: 16,
          itemSize: 25,
          feature: {
            saveAsImage: {
              show: true
            }
          }
        },
        dataZoom: [
          {
            type: 'slider',
            filterMode: 'weakFilter',
            top: 'bottom'
          },
          {
            type: 'inside',
            filterMode: 'weakFilter'
          }
        ],
        grid: {
          containLabel: true,

          left: '5%',
          right: '10%'
        },
        xAxis: {
          type: 'time',
          axisLabel: {
            formatter: (value, index) => {
              return this.moment(value).format('YYYY-MM-DD HH:mm:ss');
            }
          }
        },
        yAxis: {
          data: this.formatLabel(this.chartData)

        },
        series
      });
      const handleResize = debounce(this.myChart.resize, 300);
      window.addEventListener('resize', () => {
        handleResize();
      });
    },
    /**
     * @description: 格式化甘特图数据项
     * @param {*} data
     * @return {*}
     */
    formatMaintData(data) {
      const res = [];
      data.forEach((d, index) => {
        d.deviceLiveReportVo.timeList.forEach(t => {
          res.push({
            name: t.state - 1,
            value: [index, t.startTime, t.endTime]

          });
        });
      });

      return res;
    },
    // 生成Y轴标签
    formatLabel(data) {
      return data.map(el => el.deviceName);
    },
    // 自定义系列绘制函数
    renderRect(params, api) {
      const index = api.value(0);
      const start = api.coord([api.value(1), index]);
      const end = api.coord([api.value(2), index]);
      const height = Math.min(api.size([0, 1])[1] * 0.6, 50);

      const rectShape = this.$echarts.graphic.clipRectByRect(
        {
          x: start[0],
          y: start[1] - height / 2,
          width: end[0] - start[0],
          height: height
        },
        {
          x: params.coordSys.x,
          y: params.coordSys.y,
          width: params.coordSys.width,
          height: params.coordSys.height
        }
      );
      return (
        rectShape && {
          type: 'rect',
          transition: ['shape'],
          shape: rectShape,
          style: api.style()
        }
      );
    }
  }
};
